:root {
  --form-box-shadow: inset 0 --border-width 0.1875rem $color-gold;
  --form-box-shadow-focus: var(--form-box-shadow), 0 0 0.3125rem #{$action-color};
}

form {
  > .form-wrapper {
    padding: 0.1em 1em;
    box-shadow: 0px 0px 2px 1px $color-gold;
//  border: 1px solid $color-gold;
    margin: .75em 0;

  }
}

#edit-actions {
  box-shadow: none;
}

fieldset {
  background-color: transparent;
  border: 0;
  margin: 0;
  padding: 0;
}

legend {
  font-weight: 600;
  margin-bottom: var(--spacing--small);
  padding: 0;
}

label {
  display: block;
  font-weight: 600;
//margin-bottom: var(--spacing--small);
}

input,
select,
textarea {
  display: block;
  font-family: var(--font-family);
  font-size: 1rem;
//background-color: $form-input-bg-color;
}

details.form-wrapper {
  border: var(--form-border);
}

[type="color"],
[type="date"],
[type="datetime"],
[type="datetime-local"],
[type="email"],
[type="month"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="time"],
[type="url"],
[type="week"],
input:not([type]),
textarea {
  color: $color-black;
  appearance: none;
//background-color: $form-input-bg-color;
  border: 1px solid $form-input-border-color;
  border-radius: var(--border-radius);
  box-shadow: var(--form-box-shadow);
  box-sizing: border-box;
  margin-bottom: var(--spacing--small);
  padding: calc(var(--spacing) / 3);
  transition: border-color var(--duration) var(--timing);
  width: 100%;
  &:hover {
//  background-color: $form-input-bg-color;
    border-color: $color-gold;
  }
  &:focus-visible {
//  background-color: $form-input-bg-color;
    border: var(--input-border);
  }

  &:focus {
//  background-color: $form-input-bg-color;
    box-shadow: var(--form-box-shadow-focus);
  }

  &:disabled {
    cursor: not-allowed;

    &:hover {
      border: var(--border);
    }
  }

  &::placeholder {
    color: $base-fg-color;
    opacity: 0.25;
  }
}

[type="search"] {
  -webkit-appearance: textfield;
}

textarea {
  resize: vertical;
}

[type="checkbox"],
[type="radio"] {
  display: inline;
  margin-right: var(--spacing--small);
//accent-color: $color-dark-gold;
  &:focus {
//  focus-outline: $color-gold;
  }
  :after {
    content: " ";
//  background-color: orange;
    display: inline-block;
    visibility: visible;
  }
}

[type="file"] {
  margin-bottom: var(--spacing--small);
  width: 100%;
}

select {
  margin-bottom: var(--spacing--small);
  width: 100%;
  color: $color-black;
  background-color: #fff;
}

[type="checkbox"],
[type="radio"],
[type="file"],
select {
  &:focus {
    outline: var(--focus-outline);
    outline-offset: var(--focus-outline-offset);
  }
}

// Place the date and time on one line
.container-inline {
  .form-type-date {
    margin: 0;
    display: inline-block;
  }
}

.field--type-language {
  display: none;
}

.form-item {
  margin: 0;
}

div.form-item.form-type-checkbox {
  margin: 0 0 0 2rem;

}

// Indent radio buttons
.form-radios {
  .form-item.form-type-radio {
    margin: .1em 0 .1em 1em;
  }
}

